.section-hero {
  position: relative;
  padding-bottom: 10px;
  
  @include desktop {
	padding-top: 40px;
	padding-bottom: 100px;
  }
  
  &::before {
	position: absolute;
	z-index: -1;
	bottom: 0;
	right: 0;
	width: 1440px;
	height: 100%;
	background-size: 113% 56%;
	background-position: 88% bottom;
	background-repeat: no-repeat;
	pointer-events: none;
	content: "";
	
	@include desktop {
	  &::before {
		right: unset;
		left: 50%;
		background-size: 100% 100%;
		background-position: top center;
		transform: translateX(-50%);
		mask-image: linear-gradient(
						to right,
						transparent 0%,
						white 15%,
						white 85%,
						transparent 100%
		);
	  }
	}
  }
  
  @include desktop {
	&::before {
	  min-width: 1500px;
	  height: calc(100% + 148px);
	}
  }
  
  &__content {
  }
  
  &__header {
	margin-bottom: 30px;
	text-align: center;
	
	@include desktop {
	  margin-bottom: 48px;
	}
  }
  
  &__title {
	font-size: 32px;
	line-height: 1.2;
	font-weight: 600;
	
	@include desktop {
	  margin-bottom: 20px;
	  font-size: 48px;
	  line-height: 52px;
	}
  }
  
  &__text {
	display: none;
	font-size: 18px;
	line-height: 20px;
	
	@include desktop {
	  display: block;
	}
  }
  
  &__card-grid {
	display: grid;
	grid-template: auto / 100%;
	grid-gap: 20px;
	padding-left: 34px;
	padding-right: 34px;
	
	@include desktop {
	  grid-template: auto / repeat(4, minmax(0, 1fr));
	  grid-gap: 12px;
	  padding-left: 0;
	  padding-right: 0;
	}
  }
  
  &__card {
	min-height: 414px;
	
	@include desktop {
	  min-height: unset;
	  
	  &:nth-child(2n) {
		transform: translateY(30px);
	  }
	}
  }
}

// theme
.theme-light {
  .section-hero {
	&::before {
	  background-image: url("../images/main/hero-bg-theme-light.png");
	}
  }
}

.theme-dark {
  .section-hero {
	&::before {
	  background-image: url("../images/main/hero-bg-theme-dark.png");
	}
  }
}
